import React, { FC } from 'react';
import { Typography } from 'antd';
import styles from './PriceForm.module.less';
import ButtonApp from '../../../ui/ButtonApp/ButtonApp';

const { Title, Text } = Typography;

const PriceForm: FC = () => {
  return (
    <div className={styles.PriceForm}>
      <Title level={5}> :</Title>
      <div className={styles.priceItem}>
        <div>
          <Text className={styles.listTitle}> </Text>
          <Text className={styles.listText}>,  42</Text>
        </div>
      </div>
      <div className={styles.priceItem}>
        <div>
          <Text className={styles.listTitle}></Text>
          <Text className={styles.listText}>Hyndai, i30 N</Text>
        </div>
      </div>
      <div className={styles.priceFinished}>
        <Text>
          <b></b>:  10 000  32 000
        </Text>
      </div>
      <ButtonApp disabled> </ButtonApp>
    </div>
  );
};

export default PriceForm;
